<template>
  <div>
    <LivePlayer
      @snapOutside="snapOutside"
      ref="player"
      :videoUrl="src"
      live
      digitalZoom
      :controls="false"
    />
  </div>
</template>

<script>
import LivePlayer from "@liveqing/liveplayer";

export default {
  components: {
    LivePlayer,
  },
  props: {
    msg: String,
    src: String,
  },
  methods: {
    pause: function () {
      this.$refs.player.pause();
    },
    play: function () {
      this.$refs.player.play();
    },
    snap: function () {
      this.$refs.player.snap();
    },
    snapOutside: function (snapData) {
      alert(snapData);
    },
  },
  beforeDestroy() {
    this.$emit("closeVideo");
  },
};
</script>

<style lang="scss" scoped></style>
